import React, { useEffect, useState } from "react";
import ReactPlayer from "react-player";

function NewsBanner() {
  const [isIpad, setIsIpad] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
    };

    handleResize();

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  useEffect(() => {
    const handleResize = () => {
      setIsIpad(window.innerWidth <= 1024 && window.innerWidth >= 600); // Assuming iPad width is less than or equal to 768px
    };

    // Initial check
    handleResize();

    // Add event listener for window resize
    window.addEventListener("resize", handleResize);

    // Remove event listener on component unmount
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  return (
    <div>
      <div className="banner-container">
        {" "}
        {isMobile ? (
          <>
            <div className="video-container">
              <ReactPlayer
                url={process.env.PUBLIC_URL + "/videos/news_mobile.mp4"}
                playing={true}
                playsinline
                loop={true}
                volume={0}
                controls={false}
                width="100%"
                height="auto"
                className="player"
              />
              <div className="text-overlay">
                <h1>Nyheter</h1>
                <p>
                  Hr uppdaterar vi regelbundet och publicerar vlresearchade
                  artiklar, tips fr energibesparing och
                  skerhetsrekommendationer. Du hittar ven nyheter om vad som
                  hnder hos oss med nya projekt och annan vrig information.
                </p>
              </div>
            </div>
          </>
        ) : isIpad ? (
          <>
            <div className="video-container">
              <ReactPlayer
                url={process.env.PUBLIC_URL + "/videos/news_mobile.mp4"}
                playing={true}
                playsinline
                loop={true}
                volume={0}
                controls={false}
                width="100%"
                height="auto"
                className="player"
              />
              <div className="text-overlay">
                <h1>Nyheter</h1>
                <p>
                  Hr uppdaterar vi regelbundet och publicerar vlresearchade
                  artiklar, tips fr energibesparing och
                  skerhetsrekommendationer. Du hittar ven nyheter om vad som
                  hnder hos oss med nya projekt och annan vrig information.
                </p>
              </div>
            </div>
          </>
        ) : (
          <>
            <ReactPlayer
              url={process.env.PUBLIC_URL + "/videos/news_desktop.mp4"}
              playing={true}
              playsinline
              loop={true}
              volume={0}
              controls={false}
              width="100vw"
              // height="599px"
              height="auto"
              className="player"
            />
            <div className="banner-text2">
              <h1>Nyheter</h1>
              <p>
                Hr uppdaterar vi regelbundet och publicerar vlresearchade
                artiklar, tips fr energibesparing och
                skerhetsrekommendationer. Du hittar ven nyheter om vad som
                hnder hos oss med nya projekt och annan vrig information.
              </p>
            </div>
          </>
        )}
      </div>
    </div>
  );
}

export default NewsBanner;
